<template>
  <div id="add-blog">
    <h2>新增文章</h2> <br>
    <form v-if="!submitted">
      <h5>標題:</h5>
      <input type="text" v-model.lazy="blog.title" required /> <br>
      <h5>摘要(請勿超過50字)</h5>
      <input type="text" v-model.lazy="blog.sum" :maxlength="50" required/> 
      <h5>內容:</h5>
      <v-md-editor v-model="blog.content" height="400"/>
      <br>
      <div id="checkboxes">
        <h5>科目:</h5>
        
        <label>國文科</label>
        <input type="checkbox" value="國文" v-model="blog.hashTags" />
        <label>英文科</label>
        <input type="checkbox" value="英文" v-model="blog.hashTags" />
        <label>數學科</label>
        <input type="checkbox" value="數學" v-model="blog.hashTags" />
        <label>物理科</label>
        <input type="checkbox" value="物理" v-model="blog.hashTags" />
        <label>化學科</label>
        <input type="checkbox" value="化學" v-model="blog.hashTags" />
        <label>生物科</label>
        <input type="checkbox" value="生物" v-model="blog.hashTags" />
        <label>地科</label>
        <input type="checkbox" value="地科" v-model="blog.hashTags" />
        <label>地理科</label>
        <input type="checkbox" value="地理" v-model="blog.hashTags" />
        <label>歷史科</label>
        <input type="checkbox" value="歷史" v-model="blog.hashTags" />
        <label>公民科</label>
        <input type="checkbox" value="公民" v-model="blog.hashTags" />
        <label>資訊科</label>
        <input type="checkbox" value="資訊" v-model="blog.hashTags" />
      </div>
      <label>作者:</label>
      <input type="text" v-model.lazy="blog.author" required />
      <hr/>
      <button v-on:click.prevent="post">Add Blog</button>
    </form>
    <div v-if="submitted">
      <h3>Thanks for adding your post</h3>
    </div>
    
  </div>
</template>

<script>
import { db } from "@/firebaseConf.js";
import { collection, addDoc } from "firebase/firestore"; 
import "@kangc/v-md-editor/lib/style/base-editor.css";

export default {
  data() {
    return {
      subjects: ["國文","英文","數學","物理","化學","生物","地科","地理","歷史","公民","資訊"],
      blog: {
        title: "",
        content: "",
        hashTags: [],
        author: "",
        sum: ""
      },
      submitted: false,
    };
  },
  methods: {
    async post() {
      if (this.blog.title && this.blog.content) {
        try {
					const docRef =  addDoc(collection(db,"blogs"), {
						title: this.blog.title,
						content: this.blog.content,
						hashTags: this.blog.hashTags,
						author: this.blog.author,
            sum: this.blog.sum
					});
					alert("成功新增", docRef.id);
          this.$router.push("/blogs");
				} catch (e) {
					alert("Error adding document: ", e);
				}
      } else {
          alert('Please fill in the title & content');
      }
    },
  },
};
</script>

